@import "../styles/variables.scss";

.download-wrapper {
  width: 100%;
  height: 100%;
  background-color: $template-bg;
  overflow-y: hidden;
  .download-scroll {
    overflow-y: scroll;
    width: 100%;
    height: 100%;
    .download-spacing {
      padding: 40px 0;

      .download-content {
        margin: 0 auto;
        width: 8.3in;
        aspect-ratio: 1/1.414;
      }
    }
  }
}
