.form-row {
  display: flex;
  gap: 10px;
}

.form-col {
  display: flex;
  flex-direction: column;
  gap: 5px;
  flex-grow: 1;
}

.app-wrapper {
  width: 100%;
  height: 100vh;
  display: flex;
  .app-container {
    margin-top: 60px;
    width: 100%;
    display: flex;
  }
}
